/* ----------------------------------- COLORS ----------------------------------- */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  font-family: "Bai Jamjuree", sans-serif;
  font-size: 18px;
}

body {
  background-color: hsl(0deg, 0%, 100%);
  text-align: center;
}

/* --------------------- TEXT GENERAL ------------------- */
h1, h2, h3 {
  color: hsl(210deg, 10%, 33%);
  font-weight: 600;
}

h1 {
  font-size: 1.8rem;
  margin-bottom: 1rem;
}

h2 {
  font-size: 1.6rem;
  margin-bottom: 1rem;
}

h3 {
  font-size: 1.3rem;
  margin-bottom: 0.5rem;
}

.paragraph {
  color: hsl(201deg, 11%, 66%);
  font-size: 0.94rem;
  font-weight: 400;
  line-height: 1.4rem;
  margin-bottom: 3rem;
}

/* --------------------- BUTTON GENERAL ------------------- */
.buttons-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.2rem;
}

.btn {
  width: 100%;
  max-width: 310px;
  padding: 0.9rem 0 calc(0.9rem - 3px);
  border-radius: 10rem;
  color: hsla(0deg, 0%, 100%, 0.8);
  text-decoration: none;
  font-weight: 700;
  letter-spacing: 0.05rem;
  transition: background-color 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
}

.ios {
  background-color: hsl(171deg, 66%, 44%);
  border-bottom: 3px solid #1d907f;
}
.ios:hover {
  background-color: #2acfb7;
  box-shadow: 0 10px 10px 0px rgba(38, 186, 164, 0.2);
}

.mac {
  background-color: hsl(233deg, 100%, 69%);
  border-bottom: 3px solid #2e46ff;
}
.mac:hover {
  background-color: #7a8aff;
  box-shadow: 0 10px 10px 0px rgba(97, 115, 255, 0.2);
}

/* --------------------- PAGE ------------------- */
.page-container {
  display: flex;
  align-items: center;
  flex-direction: column;
}

header {
  background-image: url(../img/bg-header-mobile.png);
  background-repeat: no-repeat;
  background-size: 100%;
  width: 100%;
}
header .header-wrapper {
  width: calc(100% - 50px);
  margin: 18vh auto 8.5rem;
}
header .header-wrapper img {
  margin-bottom: 3rem;
}

main {
  width: calc(100% - 50px);
}
main img {
  width: 100%;
}
main #section1 {
  margin-bottom: 8rem;
}
main #section1 .ul-img-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 4rem;
  padding: 0 0.5rem;
  gap: 3rem;
}
main #section1 .ul-img-wrapper ul {
  list-style: none;
}
main #section1 .ul-img-wrapper ul .paragraph {
  margin-bottom: 2.7rem;
}
main #section2 {
  margin-bottom: 4rem;
  overflow: hidden;
}
main #section2 img {
  display: block;
  width: 105%;
  transform: translateX(-2.5%);
}
main #section3 {
  margin-bottom: 6.5rem;
}
main #section3 ul {
  list-style: none;
  margin-top: 4rem;
}
main #section3 ul li img {
  width: 40px;
  margin-bottom: 2rem;
}
main #section3 ul .paragraph {
  margin-bottom: 2.7rem;
}
main #section4 {
  margin-bottom: 5.5rem;
}
main #section4 ul {
  list-style: none;
  display: flex;
  flex-direction: column;
}
main #section4 ul li {
  margin-bottom: 2.5rem;
}
main #section4 ul li img {
  flex: 1;
  max-width: 140px;
}
main #section5 {
  margin-bottom: 9rem;
}
main #section5 .paragraph {
  margin-bottom: 2rem;
}

footer {
  background-color: #f2f2f2;
  width: 100%;
  padding-top: 2.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
footer img {
  max-width: 54px;
  margin-bottom: 2rem;
}
footer .internal-links {
  list-style: none;
  margin-bottom: 1rem;
}
footer .internal-links li {
  margin-bottom: 1.5rem;
}
footer .internal-links li a {
  text-decoration: none;
  color: hsl(210deg, 10%, 33%);
}
footer .internal-links li a:hover {
  color: hsl(171deg, 66%, 44%);
}
footer .social-links {
  list-style: none;
  display: flex;
  gap: 2rem;
}
footer .social-links li {
  width: 24px;
}
footer .social-links li a {
  width: 100%;
}
footer .social-links li a img {
  width: 100%;
  object-fit: cover;
}